import axios from "axios";
import type { AxiosInstance } from "axios";
import { ZPRequestInterceptors, ZPRequestConfig } from "./types";
import { showLoading, hideLoading } from "./loading"; //loading
const DEAFULT_LOADING = false; //,loading

class ZPRequest {
  //axios.create
  instance: AxiosInstance;
  //  
  interceptors?: ZPRequestInterceptors;
  isLoading: boolean;
  //constructor
  constructor(config: ZPRequestConfig) {
    this.instance = axios.create(config);
    this.interceptors = config.interceptors;
    //config.isLoadingtrue
    this.isLoading = config.isLoading ?? DEAFULT_LOADING;
    /*  */
    this.instance.interceptors.request.use(
      this.interceptors?.requestInterceptor,
      this.interceptors?.requestInterceptorCatch
    );
    this.instance.interceptors.response.use(
      this.interceptors?.responseInterceptor,
      this.interceptors?.responseInterceptorCatch
    );

    /*  */
    this.instance.interceptors.request.use(
      (config) => {
        //isLoadingtrueloading
        if (this.isLoading === true) {
          showLoading();
        }
        return config;
      },
      (err) => {
        return err;
      }
    );
    this.instance.interceptors.response.use(
      (res) => {
        const data = res.data;
        //loading
        hideLoading();
        //
        if (data.returnCode === "-1001") {
          console.log(",");
        } else {
          return data;
        }
      },
      (err) => {
        //
        if (err.response.status === 404) {
          console.log("404,");
        }
        return err;
      }
    );
  }
  /*  */
  //instancerequestconfig
  request<T = any>(config: ZPRequestConfig<T>): Promise<T> {
    return new Promise((resolve, reject) => {
      //config.isLoadingfalseloading
      if (config.isLoading === true) {
        this.isLoading = config.isLoading;
      }
      //config
      if (config.interceptors?.requestInterceptor) {
        //config
        config = config.interceptors.requestInterceptor(config);
      }
      this.instance
        .request<any, T>(config)
        .then((res) => {
          //config
          if (config.interceptors?.responseInterceptor) {
            //
            res = config.interceptors.responseInterceptor(res);
          }
          this.isLoading = DEAFULT_LOADING; //
          resolve(res);
          hideLoading();
        })
        .catch((err) => {
          this.isLoading = DEAFULT_LOADING; //
          reject(err);
          return err;
        });
    });
  }
  get<T = any>(config: ZPRequestConfig<T>): Promise<T> {
    return this.request<T>({ ...config, method: "GET" });
  }

  post<T = any>(config: ZPRequestConfig<T>): Promise<T> {
    return this.request<T>({ ...config, method: "POST" });
  }

  delete<T = any>(config: ZPRequestConfig<T>): Promise<T> {
    return this.request<T>({ ...config, method: "DELETE" });
  }

  patch<T = any>(config: ZPRequestConfig<T>): Promise<T> {
    return this.request<T>({ ...config, method: "PATCH" });
  }
}
export default ZPRequest;
