import { ElLoading } from "element-plus";
let loadingCount = 0;
let loading: any;

const startLoading = () => {
  loading = ElLoading.service({
    lock: true, // 
    spinner: "el-icon-loading", // loading
    text: "...", // 
    target: ".el-loading", // 
    background: "none", // 
    customClass: "mask", // 
  });
};

const endLoading = () => {
  loading.close();
};

export const showLoading = () => {
  if (loadingCount === 0) {
    startLoading();
  }
  loadingCount += 1;
};

export const hideLoading = () => {
  if (loadingCount <= 0) {
    return;
  }
  loadingCount -= 1;
  if (loadingCount === 0) {
    endLoading();
  }
};
